@import "variables";
@import "mixin";

body{
  font-size: 20px;
  line-height: 22px;
  color: #474747;
  .header{
    width: 100%;
    position: absolute;
    top: 0;
    .containerParent();
    .container{
      .containerChild();
      display: grid;
      grid-template-areas: ". headline headline"
                           "logo search cabinet"
                           "navPanel navPanel navPanel"
                           "secondNav secondNav secondNav";
      grid-template-columns: 25% 1fr 25%;
      .header__headline{
        grid-area: headline;
        p{
          font: 1em/2em @regular;
          color: #474747;
          text-align: center;
          background-color: #ececec;
          border-bottom-right-radius: 15px;
          border-bottom-left-radius: 15px;
        }
      }
      .header__logo{
        grid-area: logo;
        img{
          width: 70%;
        }
      }
      .header__mainSearch{
        grid-area: search;
        display: flex;
        flex-wrap: wrap;
        .search{
          width: 100%;
          margin-top: 70px;
          input{
            width: 90%;
            height: 45px;
            padding-left: 25px;
            font: 1.125em/45px @regular;
            color: #9e9e9e;
            border: 3px solid @mainColor;
            border-radius: 50px;
            outline: none;
          }
          i{
            font-size: 1.5em;
            position: relative;
            left: -50px;
            top: 2px;
            color: @mainColor;
          }
        }
        .numbers{
          margin-top: 50px;
          ul{
            display: flex;
            li{
              padding: 0 10px;
              i{
                display: flex;
                justify-content: center;
                align-items: center;
                width: 65px;
                height: 65px;
                font-size: 2em;
                color: @mainColor;
                border: 3px solid @mainColor;
                border-radius: 50px;
              }
              a{
                font: 1.6em/2em @regular;
                color: #474747;
                text-decoration: none;
              }
            }
          }
        }
        .workTime{
          margin: 55px 0 0 60px;
          display: flex;
          i{
            font-size: 1.5em;
            line-height: 1.725em;
            color: @mainColor;
          }
          ul{
            margin-left: 20px;
            li{
              span{
                font: .7em/1em @regular;
              }
            }
          }
        }
      }
      .header__cabinet{
        grid-area: cabinet;
        margin-top: 70px;
        ul {
          display: flex;
          flex-wrap: wrap;
          justify-content: space-between;
          li{
            display: flex;
            justify-content: space-around;
            align-content: center;
            span{
              display: inline-block;
              font: 1.125em/1.1em @regular;
              text-align: center;
              i{
                padding: 0 20px;
                font-size: 2em;
                color: @mainColor;
              }
              &:hover{
                text-decoration: underline;
                cursor: pointer;
              }
            }
            &:nth-child(1){
              width: 45%;
            }
            &:nth-child(2){
              width: 45%;
            }
            &:nth-child(3){
              margin-top: 50px;
              padding: 10px;
              width: 310px;
              height: 50px;
              justify-content: space-between;
              align-items: center;
              border: 1px solid @mainColor;
              border-radius: 5px;
              span{
                i{
                  margin: 0 0 0 15px;
                  padding: 3px;
                  color: #fff;
                  background-color: #7d3daf;
                  border-radius: 5px;
                }

              }
            }
          }
        }
      }
      .header__nav-panel{
        grid-area: navPanel;
        &__major{
          ul{
            display: flex;
            justify-content: space-between;
            margin-top: 50px;
            li{
              width: 16.67%;
              a{
                display: inline-block;
                width: 100%;
                font: 1.125em/2em @bold;
                color: #fff;
                text-align: center;
                text-decoration: none;
                background-color: @mainColor;
                border-right: 1px solid #77c9cc;
                border-left: 1px solid #77c9cc;
                &:hover{
                  background-color: @hoverColor;
                }
              }
            }
          }
        }
        &__goods{
          ul{
            display: flex;
            justify-content: space-between;
            li{
              width: 25%;
              a{
                display: inline-block;
                width: 100%;
                font: 1.125em/2em @regular;
                color: @linkColor;
                text-align: center;
                text-decoration: none;
                background-color: #77c9cc;
                border: 1px solid @mainColor;
                &:hover{
                  color: #fff;
                  background-color: @hoverColor;
                }
              }
            }
          }
        }
      }
      .header__secondNav{
        grid-area: secondNav;
        position: relative;
        bottom: -410px;
        height: 50px;
        background-color: @mainColor;
        ul{
          display: flex;
          li{
            &:nth-child(1){
              width: 20%;
              background-color: #77c9cc;
              a{
                display: block;
                font: 1.125em/50px @bold;
                color: @linkColor;
                text-decoration: none;
                i{
                  padding: 0 15px 0 25px;
                }
                &:hover{
                  color: #fff;
                  background-color: @hoverColor;
                }
              }
            }
            &:nth-child(2){
              width: 55%;
              text-align: center;
              input{
                width: 90%;
                height: 35px;
                padding-left: 25px;
                font: 1em/50px @regular;
                color: #9e9e9e;
                border: 3px solid @mainColor;
                border-radius: 50px;
                outline: none;
              }
              i{
                font-size: 1.2em;
                line-height: 45px;
                position: relative;
                left: -50px;
                top: 2px;
                color: @mainColor;
              }
            }
            &:nth-child(3){
              width: 17%;
              a{
                display: flex;
                justify-content: space-around;
                align-items: center;
                font: 1.125em/50px @regular;
                color: #fff;
                text-decoration: none;
                i{
                  padding: 5px;
                  font-size: .875em;
                  border: 2px solid #fff;
                  border-radius: 50px;
                }
              }
            }
            &:nth-child(4){
              width: 3%;
              a{
                display: block;
                i{
                  width: 50px;
                  font-size: 1.5em;
                  line-height: 50px;
                  text-align: center;
                  color: #fff;
                  background-color: #7d3daf;
                }
              }
            }
            &:nth-child(5){
              width: 15%;
              a{
                display: flex;
                justify-content: space-around;
                align-items: center;
                font: 1.125em/50px @regular;
                color: #fff;
                text-decoration: none;
              }
            }
          }
        }
      }
    }
  }
  .content{
    width: 100%;
    .containerParent();
    .container{
      .containerChild();
      .content__actualProduct,
      .content__saleProduct{
        .actualProduct__headline,
        .saleProduct__headline{
          display: flex;
          justify-content: space-between;
          h5{
            font: 2.25em/2.375em @italic;
            color: @linkColor;
          }
          div{
            display: flex;
            margin: 45px 40px 0 0;
            p{
              font: .875em/1em @regular;
              margin-right: 10px;
            }
            :nth-child(2){
              color: #fff;
              background-color: @mainColor;
            }
            span{
              width: 130px;
              height: 25px;
              margin: 5px 5px 0;
              font: .875em/25px @regular;
              text-align: center;
              border: 1px solid @mainColor;
              border-radius: 5px;
              &:hover{
                cursor: pointer;
              }
            }
          }
        }
        .actualProduct__slider,
        .saleProduct__slider{
          display: flex;
          img{
            width: 200px;
            height: 350px;
            border-radius: 5px;
          }
          .slider{
            width: 1340px;
            height: 350px;
            padding: 0 30px;
            position: relative;
            left: 50px;
            .lSSlideOuter {
              ul {
                .item-a,
                .item-b,
                .item-c,
                .item-e,
                .item-d,
                .item-f {
                  width: 250px;
                  height: 350px;
                }
                li {
                  text-align: center;
                  border: 1px solid #e6e6e6;
                  border-radius: 5px;
                  img{
                    margin-top: 15px;
                    width: 200px;
                    height: 200px;
                  }
                  p{
                    padding: 10px;
                    font: 1em/1.125em @regular;
                    color: @linkColor;
                    text-align: left;
                  }
                  .price{
                    font: 1.25em/1.375em @bold;
                    span{
                      font: .875em/1em @regular
                    }
                  }
                  &:hover{
                    cursor: pointer;
                  }
                }
              }
              .lSAction{
                a{
                  background-image: none;
                }
              }
              .cS-hidden {
                height: 1px;
                opacity: 0;
                filter: alpha(opacity=0);
                overflow: hidden;
              }
            }
          }
        }
      }
    }
  }
}
